      h1 Surtido

    .row.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-8.col-md-12.col-12.mb-4
        p.mb-4 Es el conjunto de productos ofertados por los diferentes establecimientos comerciales y que se encuentran dispuestos para que el consumidor los compre. El surtido se refiere a la variedad de los productos y no a la cantidad, sus objetivos son la satisfaccin de las necesidades y deseos de los clientes, y la maximizacin en la rentabilidad del establecimiento, la cual se mide por metro cuadrado. 
        p.mb-4 En otras palabras, para definir el surtido se puede decir que es el grupo de referencias o artculos disponibles en el punto de venta. La rentabilidad de la empresa depender del valor de la marca, la rotacin y composicin del surtido (Puente, 2004).
        p.mb-4 Contar con un buen surtido es crucial para los establecimientos, ya que influye significativamente en la imagen de la marca. Un surtido adecuado ayuda a definir la personalidad de la marca y contribuye en gran medida al xito empresarial.  
        .cajon.color-acento-contenido.p-4
          p.mb-0 #[b La coherencia del surtido:] es el equilibrio que se consigue entre productos segn los criterios de rotacin, margen, superficie y lineal disponible, motivacin de la clientela, necesidades permanentes y estacionales. La eleccin del surtido es un acto fundamental ya que, al determinarlo, se satisface a un sector de clientela en detrimento de otro (Salen, 1994).
      .col-lg-4.col-md-7.col-8
        img(src='@/assets/curso/tema2/1.png', alt='tema1')

    separador
    #t_2_1.titulo-segundo.mb-4
      h2 2.1	Estructura del surtido

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      .col-lg-4.col-md-9.col-9.mb-4
        img.d-none.d-lg-block(src='@/assets/curso/tema2/2.png', alt='tema1')
      .col-lg-8.col-md-12.col-12.mb-4
        p.mb-3 Se refiere a los niveles, los cuales se dividen por departamentos, secciones, categoras, familias, subfamilias, artculos y referencias. Por ejemplo, la familia de los licores con productos de vinos comunes, vinos selectos, whisky, ron, aguardiente, brandy, etc.
        p.mb-3 La cantidad de referencias que pone en venta un establecimiento est condicionada por el tamao del local. Evidentemente, los establecimientos que posean ms tamao tendrn la posibilidad de poner en venta una mayor cantidad de referencias. 
        p.mb-3 Los supermercados, que suelen tener ms o menos entre 1.000 m y 2,500 m, pueden poner en venta aproximadamente 5.300 referencias, y los autoservicios, que son ms pequeos en tamao, mximo 2.900 referencias. Otras variables que influyen en la cantidad de referencias que trabaja un local comercial, son los hbitos de compra del consumidor, la imagen que tenga el establecimiento y su poltica comercial.
        p.mb-3 La clasificacin o estructura del surtido radica en organizar este grupo de referencias en niveles. Tiene un doble propsito: el primero es diferenciar los niveles y el segundo es identificar cules niveles poseen mayor o menor rango. En cuanto a la gestin por categoras, la clasificacin de referencias se puede distinguir comenzando en la totalidad del surtido y finalizando en las referencias de los artculos; como se puede ilustra en la siguiente figura.

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      .col-lg-10.col-md-12.col-12.mb-4
        
        ImagenInfografica.color-primario
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/tema2/3.jpg', alt='')
            .tarjeta.colore.p-4(x="5%" y="85.2%" numero="")
              .h5.mb-2 Subfamilias
              p Se refiere a artculos similares pero que tienen una ligera diferencia, como, por ejemplo: yogures naturales sin azcar, azucarados o con frutas. La subfamilia para la higiene infantil seran paales, copitos, champ para nios, lociones, cremas, etc.
            .tarjeta.colore.p-4(x="10%" y="60%" numero="")
              .h5.mb-2 Familias
              p Se refiere a artculos que satisfacen una misma necesidad genrica, creando a su vez, distintas categoras de productos. Por ejemplo: las familias de la categora de aseo personal seran jabones para el cuerpo, champ, higiene infantil, etc.
            .tarjeta.colore.p-4(x="20%" y="40%" numero="")
              .h5.mb-2 Subcategoras
              p Son divisiones existentes dentro de una categora.
            .tarjeta.colore.p-4(x="30%" y="20%" numero="")
              .h5.mb-2 Categoras de productos
              p Corresponden a una unidad de necesidad y responden a un mismo objetivo global. Segn la definicin de una categora de productos un artculo podr estar ubicado en un lugar o en otro, porque puede responder a distintas necesidades.
            .tarjeta.colore.p-4(x="40%" y="5%" numero="")
              .h5.mb-2 Segmentos
              p Son divisiones existentes dentro de una subcategora.
            .tarjeta.colore.p-4(x="50%" y="90%" numero="")
              .h5.mb-2 Referencias
              p Las diferentes opciones en tamao, color, sabor, etc., que tienen los productos.
            .tarjeta.colore.p-4(x="69%" y="40%" numero="")
              .h5.mb-2 Artculos
              p Tambin se les puede llamar productos o por su nombre comn y natural como por ejemplo: leche entera, semideslactosada o deslactosada.
            .tarjeta.colore.p-4(x="85%" y="60%" numero="")
              .h5.mb-2 Divisiones
              p Tambin se conocen como grupos. Por ejemplo: divisiones de alimentacin y no alimentacin.
            .tarjeta.colore.p-4(x="95%" y="85%" numero="")
              .h5.mb-2 Departamentos
              p Tambin se les conoce como sectores. Forman los grandes centros de actividades del establecimiento que agrupan varias secciones para tener un surtido homogneo y complementario.
    
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      .col-lg-10.col-md-12.col-12.mb-4
        .cajon.color-secundario.p-4
          p.mb-0 Entonces, la clasificacin o estructura del surtido se refiere a distribuir los artculos en grupos o niveles para que la oferta comercial se pueda clasificar, identificar y gestionar estratgicamente. Lo normal es que cada establecimiento disee su propia estructura, sin embargo, cabe anotar que la empresa debe gestionar de forma estratgica el surtido y su clasificacin, teniendo en cuenta variables como la tipologa del negocio y la filosofa empresarial. 

    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2 Dimensiones del surtido

    p.mb-4(data-aos="fade-right") Las dimensiones del surtido cuentan con las siguientes caractersticas:

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .col-lg-12   
        .tarjeta--container.row.mb-4
          .col-md.tarjeta.color-primario.bd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Amplitud
