                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/4.svg', alt='Texto que describa la imagen')
            p.text-center Nmero de familias diferentes de productos que ofrece la marca.
          .col-md.tarjeta.color-primario.bdd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Profundidad
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/5.svg', alt='Texto que describa la imagen')
            p.text-center Nmero de variantes que la marca ofrece por cada producto en cada familia. 
          .col-md.tarjeta.color-primario.bd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Longitud
              .col-8
                figure
                  img.img-a.img-t.my-2(src='@/assets/curso/tema2/6.svg', alt='Texto que describa la imagen')
            p.text-center Nmero total de productos que tiene la marca en una familia.
          .col-md.tarjeta.color-primario.bdd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Consistencia
              .col-8
                figure
                  img.img-a.img-t.my-2.mt-3(src='@/assets/curso/tema2/7.svg', alt='Texto que describa la imagen')
            p.text-center Cercana relativa entre las lneas de producto y su uso final.
    
    p.mb-5(data-aos="fade-right") A continuacin, se presenta cmo se organizan dichas dimensiones del surtido:

    .row.fondo1.align-items-center.p-3.mb-5(data-aos="fade-down")
      .col-lg-12.col-md-12.col-12.px-lg-5.px-4
        .row.justify-content-center.mb-4
        
        .tarjetaf.p-4.mb-4
          SlyderA(tipo="b").slider-a-custom(data-aos="zoom-in-left")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Surtido amplio y profundo
                p.mb-4 Es cuando un establecimiento comercial quiere satisfacer de manera amplia una gran variedad de necesidades. Para ello, debe haber muchas familias y que cada familia posea muchas referencias.
              .col-lg-5
                .titulo-sexto.color-secundario.mb-3
                  h5 Figura 7. 
                  | Surtido amplio y profundo
                img(alt="Presenta cuando un establecimiento comercial busca satisfacer una amplia gama de necesidades, ofreciendo a profundidad muchas familias de productos, cada una con numerosas referencias." src="@/assets/curso/tema2/8.svg")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Surtido amplio y poco profundo
                p.mb-4 Muy normal en autoservicios y tiendas de descuento. Se da cuando el establecimiento tiene muchas familias y cada familia posee poca variedad de artculos.
              .col-lg-5
                .titulo-sexto.color-secundario.mb-3
                  h5 Figura 8. 
                  | Surtido amplio y poco profundo
                img(alt="Presenta cuando el establecimiento ofrece muchas familias de productos, pero cada familia tiene una variedad limitada de artculos." src="@/assets/curso/tema2/9.svg")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Surtido estrecho y profundo
                p.mb-4 Muy normal en tiendas especializadas, porque presenta buena variedad de productos, pero solo para algunas familias especficas. Por ejemplo, las tiendas de electrodomsticos.
              .col-lg-5
                .titulo-sexto.color-secundario.mb-3
                  h5 Figura 9. 
                  | Surtido estrecho y profundo
                img(alt="Presenta  buena variedad de productos, pero solo en algunas familias especficas, como en las tiendas de electrodomsticos." src="@/assets/curso/tema2/10.svg")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Surtido estrecho y poco profundo
                p.mb-4 Muy normal en las tiendas tradicionales de barrio. Se da cuando el establecimiento posee pocas familias y pocas referencias. Otro ejemplo son los concesionarios de autos.
              .col-lg-5
                .titulo-sexto.color-secundario.mb-3
                  h5 Figura 10. 
                  | Surtido estrecho y poco profundo
                img(alt="Presenta cuando el  establecimiento tiene pocas familias y pocas referencias." src="@/assets/curso/tema2/11.svg")
    
    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2 Determinacin de las dimensiones del surtido

    p.mb-4(data-aos="fade-right") En la gestin del surtido, el primer paso es definir con precisin sus dimensiones. Para lograrlo, es necesario considerar los siguientes factores:
    
    TabsC.color-primario.mb-5(data-aos="zoom-in-left")
      .py-md-4(titulo="El tamao de la tienda")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/12.png', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 El tamao de la tienda
            p.mb-0 Se refiere a la superficie que dispone el establecimiento, y segn sea su tamao la empresa podr jugar con el diseo de la longitud y profundidad del surtido de productos. 
      .py-md-4(titulo="La rentabilidad de la tienda")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/13.png', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 La rentabilidad de la tienda
            p.mb-0 El establecimiento debe decidir qu precio se ajusta a los productos. Desde la teora, un establecimiento amplio y profundo, es decir, con una superficie grande, podra aumentar sus precios de venta con el fin de lograr mayor rentabilidad. Es normal, porque el cliente recibe a cambio el poder comprar todo lo que necesita sin necesidad de trasladarse a otro almacn, tienda o establecimiento. Pero se debe pensar en qu prefieren los clientes: mayor variedad de productos con mayores posibilidades de eleccin de productos o menor variedad de productos con menores posibilidades de eleccin de productos. 
      .py-md-4(titulo="La naturaleza de los productos")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/14.png', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 La naturaleza de los productos
            p.mb-0 Al comprar artculos comunes como pan, huevos, leche o mantequilla, los consumidores generalmente no comparan mucho, por lo que no es necesario contar con un surtido muy profundo en el establecimiento. En cambio, al adquirir productos menos frecuentes o poco comunes, como electrodomsticos, textiles o muebles, los clientes tienden a comparar ms detenidamente. En estos casos, el cliente evala diversas opciones en cuanto a precio, caractersticas, calidad y beneficios, por lo que es importante ofrecer un surtido ms amplio. 
      .py-md-4(titulo="El modo de presentacin de los productos")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/15.png', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 El modo de presentacin de los productos
            p.mb-0 Se refiere a la forma en que se exhiben los productos. Se pueden usar gndolas, determinados tipos de contenedores, #[em displays] o incluso, la presentacin de los artculos en el suelo. Es por esto por lo que el modo de presentacin de los productos influye directamente en la composicin del surtido.
      .py-md-4(titulo="El comportamiento de la clientela")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/16.png', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 El comportamiento de la clientela
            p.mb-0 Se debe tener en cuenta los hbitos de compra por parte de los clientes que, segn sus caractersticas psicolgicas y su poder adquisitivo, realizan las compras bien sea de manera banal o excepcional.
    p.mb-4(data-aos="fade-left") Para lograr un equilibrio en el establecimiento comercial se debe llegar a una optimizacin en la anchura y la profundidad del surtido, eligiendo la composicin que mejor funcione en el interior, y pensando en los factores enumerados anteriormente, en los productos y en los clientes. Adems, se deben medir de forma cuidadosa los siguientes dos valores:
    
    .row.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-8
        ul.lista-ul.ps-2
          li.d-flex.mb-4
            i.fas.fa-circle-check
            p.mb-0 #[b La demanda de cada producto:] en el interior de cada familia de artculos se debe calificar cada referencia de acuerdo con su volumen de venta.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-3 #[b La influencia de los productos de atraccin:] cules son los artculos o referencias que influyen en la venta de dems. Su sola presencia en el punto de venta se hace necesaria porque ayuda las ventas en conjunto de la familia. Por lo general, los productos de atraccin suelen ser ms rentables que los dems, ya que adems de influir en la venta de los dems, tambin son productos que tienen alta rotacin (Salen, 1994).
      .col-lg-4.col-md-9.col-9
        img.img-a.img-t(src='@/assets/curso/tema2/17.png', alt='')
    
    
    separador
    #t_2_2.titulo-segundo.mb-5
      h2 2.2	Planograma o planimetra

    .row.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-8.col-md-12.col-12.mb-4
        p.mb-3 Es una herramienta muy necesaria para la gestin eficaz del espacio donde se exhiben los productos de los establecimientos minoristas, ms an en la actualidad, porque existe un mercado ms complicado y competitivo, en el que ha aumentado el tamao de las tiendas, las dimensiones del surtido y el nmero de categoras. Esta herramienta tiene como objetivos mejorar la presentacin de los artculos y aumentar las ventas.
        p.mb-3 Se puede definir como un instrumento del #[em merchandising] que consiste en el diseo de un boceto, diagrama o croquis a travs de un #[em software], donde se describe detalladamente el lugar en el que se van a ubicar los artculos. Adems, el planograma detalla la cantidad de productos, el nmero de #[em facings] (caras) de un mismo producto, cul es la altura y qu profundidad deben ocupar las distintas referencias sealadas. Se entiende por facing al nmero de caras de un mismo producto que se necesitan para que se pueda ver por parte del cliente. Por lo tanto, el planograma viene siendo una estrategia de mercadeo que tiene como objetivo hacer visible al producto al interior del establecimiento.
        p.mb-3 La complejidad de un planograma va a depender del tamao del establecimiento, de las necesidades del detallista o del programa #[em (software)] utilizado para su diseo. Por eso se recomienda que el planograma sea sencillo pero que, a la vez, detalle toda la informacin necesaria para saber cul es la ubicacin exacta de cada artculo dentro de la tienda. 
      .col-lg-4.col-md-7.col-8.mb-4
        img(src='@/assets/curso/tema2/18.png', alt='tema1')

    .row.fondo2.align-items-center.p-0.mb-5(data-aos="fade-down")
      .col-lg-12.col-md-12.col-12.px-lg-5.px-4
        .row.justify-content-center.mb-4
        
        .subt.mb-5(data-aos="fade-right")
          .d-flex.align-items-center.mb-1
            img(src='@/assets/curso/tema1/ic.svg', alt='')
            h3.mb-0.ms-2 Las funciones principales del planograma son: 

        .row.justify-content-center(data-aos="fade-left")
          .col-lg-4.col-md-6.mb-4
            .overflow-hidden.h-100.bg5.p-4
              img.img-a.img-t(src='@/assets/curso/tema2/19.svg', alt='tema1')
              p.mb-0 #[b Mejorar la ubicacin de los productos.] Poniendo los productos ms vendidos o que son ms rentables en un nivel ms destacado.
          .col-lg-4.col-md-6.mb-4
            .overflow-hidden.h-100.bg5.p-4
              img.img-a.img-t(src='@/assets/curso/tema2/20.svg', alt='tema1')
              p.mb-0 #[b Aumentar las ventas.] Con el uso de los planogramas, los establecimientos comerciales deciden cmo va a lucir el lineal y qu artculos sern ms accesibles para el cliente; adems de procurar que el diseo sea ms impactante y atractivo para los clientes.
    
    p.mb-4(data-aos="fade-right") Y en cuanto a los beneficios: 

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .col-lg-10
        ul.lista-ul.ps-2
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Reduce el tiempo de creacin de la ubicacin de los productos en el establecimiento. 
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Establece el potencial de venta de cada rea de la superficie. 
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Mejora la satisfaccin del cliente facilitando las compras a travs de lineales ordenados y atractivos visualmente.
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Permite una revisin ms estricta del inventario al ordenar las estanteras y los #[em facings] (frentes) basndose en la rotacin, lo que da como resultado una reduccin de las roturas del #[em stock] (espacios vacos). 
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Suministra una mejor relacin del posicionamiento de los artculos. 
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Facilita la reposicin de productos porque dota al personal encargado de una gua a seguir. 
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Influencia en la conducta del consumidor al realizar una accin de compra, por lo que se logra un aumento en las ventas y en la rentabilidad. 
          li.d-flex.mb-2
            i.fas.fa-circle-check
            p.mb-0 Es una herramienta efectiva para informar al personal la disposicin determinada para el lineal. 

    p.mb-4(data-aos="fade-right") En el momento del diseo de un planograma se recomienda determinar el espacio de acuerdo con el rendimiento o rotacin de los productos. La principal regla es asegurar que exista en el lineal, mercanca suficiente que satisfaga la demanda de los clientes. 
    p.mb-4(data-aos="fade-left") Es por lo que el establecimiento debe tener en cuenta las siguientes consideraciones:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4(data-aos="fade-left" data-aos-delay="500")
        figure.m-auto.col-8.col-md-7.col-lg-12
          img.mb-4(src="@/assets/curso/tema2/21.png") 
      .col-lg-8.col-md-12.col-12
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.col-xl-11.m-auto(titulo="Emplazamiento")
            p.mb-0 Elegir el lugar del establecimiento donde se va a ubicar una determinada referencia, los artculos adyacentes y los lugares secundarios.
          .row.col-xl-11.m-auto(titulo="Extensin de la categora")
            p.mb-0 Cul es el tamao del espacio que le ser asignado a la categora.
          .row.col-xl-11.m-auto(titulo="Lugar del producto")
            p.mb-0 Cmo asignar el sitio a cada artculo. Se puede tener en cuenta por la cantidad de ventas, por rotacin, por encargos, por niveles de inventario, etc.
          .row.col-xl-11.m-auto(titulo="Disposicin")
            p.mb-0 Cmo organizar el planograma, puede ser por precios, marcas, fabricantes, etc.
          .row.col-xl-11.m-auto(titulo="Sealizacin y PLV")
            p.mb-0 Establecer si la categora pudiera beneficiarse de la sealizacin interna del local o del material PLV (Publicidad en el Lugar de Venta). 

    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2 Tipos de planogramas

    p.mb-4(data-aos="fade-right") Esencialmente hay tres tipos de planogramas:
    
    TabsC.color-primario(data-aos="zoom-in-left")
      .py-md-4(titulo="Casillas con texto")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/22.svg', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 Casillas con texto
            p.mb-0 Es el planograma ms sencillo, porque emplea recuadros para representar los artculos, ubicando su nombre adentro. Por lo general, son en blanco y negro y bidimensionales. Principalmente son usados en tiendas de alimentos o en departamentos que tienen productos con alta rotacin.
      .py-md-4(titulo="Pictricos")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/23.svg', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 Pictricos
            p.mb-0 Es un planograma ms complejo y detallado porque usan una imagen del producto y de cmo debe ser exhibido. Por lo general, se disean a escala y en colores para proveer una descripcin exacta de los productos exhibidos. Comnmente usado en establecimientos de moda y tiendas por departamento donde la exhibicin es ms relevante y la presentacin es primordial. Suelen ser imgenes bidimensionales a travs de fotos o ilustraciones que muestran en detalle el diseo de los productos.
      .py-md-4(titulo="La naturaleza de los productos")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/24.svg', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 Tridimensionales
            p.mb-3 PlanoGraphics, Panogramas PRO y Adalta Apollo Space Management & Planning, entre otros.
            p.mb-3 Cuando se establece un planograma se debe evitar:
            ul.lista-ul
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Puertas cercanas a las puertas de acceso o a los depsitos de mercancas.
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Esquinas con poca iluminacin.
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Mezclar productos de la canasta familiar con otros que tengan olores fuertes como por ejemplo detergentes o insecticidas.
            p.mb-3 Adems, se debe buscar:
            ul.lista-ul
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Crear impacto. 
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Que haya disponibilidad de artculos de manera permanente.
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Tener claridad en el precio (nmeros redondos).
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 La facilidad para el cliente.
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Que la informacin sea clara.
              li.d-flex.mb-1
                i.fas.fa-circle-check
                p.mb-0 Que exista un estmulo a la compra por parte del cliente.
    separador
    #t_2_3.titulo-segundo.mb-5
      h2 2.3 #[em Layout]
  
    p.mb-4(data-aos="fade-right") Se refiere al diseo o disposicin de los productos (bienes y servicios) en sectores o categoras en el punto de venta del establecimiento. Para el #[em retail] es un concepto tcnico tan importante que perfectamente puede estar incluido dentro de las estrategias en un plan de #[em marketing.]
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-12.col-md-12.col-12
        .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/tema2/25.svg')})`}"
          )
          .bloque-texto-g__texto.p-5
            p.mb-4 Este busca la instalacin ptima de los componentes, activos o pasivos, para alcanzar los volmenes requeridos disminuyendo el gasto en mano de obra, movimientos, existencias o almacenamientos intermedios, adems de la inactividad o espera de los equipos.
            br
            br
            |
            p En cuanto al mercadeo, el #[em layout] se refiere al emplazamiento de los bienes o servicios en cualquier tipo de establecimiento, o sea, a su organizacin estratgica con el fin de disminuir el consumo de mano de obra y certificar la correcta exhibicin de los artculos. El #[em marketing-layout] tiene como objeto aumentar las ventas por metro cuadrado, agrupando o separando los artculos con base a los criterios de consumo que identifiquen a un determinado mercado. Por ejemplo, en el tema de las estanteras se suele elegir entre dos tipos de #[em layout]: rectangular o angular (Martnez, 2016).
    
    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2  Tipos de #[em layout]

    p.mb-5(data-aos="fade-right") A continuacin, se presentan cinco (5) tipos de #[em layout] diferentes utilizados en los establecimientos:

    .row.justify-content-center(data-aos="fade-left")
      .col-lg-10
        PasosA.color-secundario.mb-5(tipo="n")
          .row.align-items-center
            .col-lg
              h4 Diseo recto
              p Normalmente usado en establecimientos que tienen venta al por menor. Puede confundir y frustrar, porque no se puede ver por encima del mobiliario; es ms econmico.
            .col-auto.mb-3
              .titulo-sexto.color-secundario.mb-3
                h5 Figura 11. 
                | Diseo recto
              figure(data-aos="zoom-in")
                img(src='@/assets/curso/tema2/26.png', alt='Presenta un diseo recto que se utiliza en los establecimientos, el cual no permite ver por enciam del mobiliario.')
          .row.align-items-center
            .col-auto.mb-3
              .titulo-sexto.color-secundario.mb-3.text-left
                h5 Figura 12. 
                | Diseo diagonal
              figure(data-aos="zoom-in")
                img(src='@/assets/curso/tema2/27.png', alt='Presenta un diseo diagonal del establecimiento, mejorando la visibilidad para los cajeros y clientes.')
            .col-lg
              h4.text-left Diseo diagonal
              p.text-left Presenta una buena distribucin del espacio para los establecimientos de autoservicio o tiendas minoristas.
              p.text-left Posee una muy buena visibilidad para los cajeros y clientes.
              p.text-left Permite el movimiento y el flujo de trfico a la tienda.
          .row.align-items-center
            .col-lg
              h4 Diseo angular
              p Es la mejor opcin para los establecimientos especializados de alta gama. Las curvas y ngulos de los muebles permiten al cliente apreciar mejor los artculos. Favorecen una mejor circulacin dentro del establecimiento.
              p Es el ms costoso por las curvas y los ngulos de los muebles.
            .col-auto
              .titulo-sexto.color-secundario.mb-3
                h5 Figura 13. 
                | Diseo angular
              figure(data-aos="zoom-in")
                img(src='@/assets/curso/tema2/28.png', alt='Presenta un diseo angular del establecimiento, con curvas y ngulos que permiten apreciar mejor los productos.')

          .row.align-items-center
            .col-auto.mb-3
              .titulo-sexto.color-secundario.mb-3.text-left
                h5 Figura 14. 
                | Diseo geomtrico
              figure(data-aos="zoom-in")
                img(src='@/assets/curso/tema2/29.png', alt='Presenta un diseo para establecimientos con bastidores que permiten mejorar la exhibicin.')
            .col-lg
              h4.text-left Diseo geomtrico
              p.text-left Es el diseo apropiado para exhibicin de ropa o tiendas departamentales.
              p.text-left Se utilizan bastidores y mobiliario especial para lucir un diseo interesante con un precio econmico.
          .row.align-items-center
            .col-lg
              h4 Diseo mixto
              p Integra diseos rectos, diagonales y con ngulos planos para generar un diseo funcional. Mueve el trfico hacia los muros y el fondo del establecimiento.
            .col-auto
              .titulo-sexto.color-secundario.mb-3
                h5 Figura 15. 
                | Diseo mixto
              figure(data-aos="zoom-in")
                img(src='@/assets/curso/tema2/30.png', alt='Presenta diseos rectos y diagonales de manera funcional.')

    h4.mb-4(data-aos="fade-right") Formatos del #[em layout]
    p.mb-5(data-aos="fade-left") Se distinguen dos (2) categoras principales, #[em layout] con transformacin y sin transformacin:

    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul").mb-5
      .row.col-xl-11.m-auto(titulo="<em>Layout</em> con transformacin")
        p.mb-4  #[b #[em Layout] por proceso:] los componentes son agrupados por la funcin especial que cumplen, sin referirse a ningn producto en especial (por ejemplo, talleres por encargo).
        p.mb-4  #[b #[em Layout] por producto:] los componentes se ordenan en funcin de las etapas por las cuales pasa el producto, desde la entrada de la materia prima hasta la salida del producto terminado (por ejemplo, montaje de automviles).
        p.mb-4  #[b #[em Layout] por posicin fija:] el producto por sus caractersticas permanece inmovilizado y los diversos medios de fabricacin requeridos se sitan alrededor del mismo (por ejemplo, aviones, astilleros). Los medios de produccin inmovilizados sirven para la fabricacin de ms de una unidad.
        p.mb-0  #[b #[em Layout] por proyecto:] se refiere a situaciones especiales, como construccin de edificios o carreteras. A veces coincide este formato con el de posicin fija, pero el de proyectos debe planearse teniendo en cuenta el terreno particular en el que se lleva a cabo la tarea, el cual puede sufrir cambios.
      .row.col-xl-11.m-auto(titulo="<em>Layout</em> sin transformacin")
        p.mb-4  #[b #[em Layout] del almacenamiento:] el objetivo es cumplir con una funcin de inventario y analiza la colocacin de los componentes en un almacn.
        p.mb-0  #[b #[em Marketing layout:]] los componentes se ubican de forma tal de facilitar la venta (por ejemplo, supermercados).
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-7
        p.mb-4 Los objetivos del #[em layout] de los establecimientos comerciales son mejorar la rapidez para preparar los pedidos, facilitar la exactitud de estos y la ubicacin ms eficiente del surtido. Si todo lo anterior se logra, se pueden potencializar las ventajas competitivas de la empresa que estn incluidas en el plan estratgico, por lo general adquiriendo ciclos de pedido con mayor rapidez y con mejor servicio al cliente.
        p.mb-4 Fases fundamentales en el diseo de un almacn:
        ul.lista-ul
          li.d-flex.mb-4
            i.fas.fa-circle-check
            p.mb-0 #[b Fase de diseo de la instalacin.] Se elige el tipo de #[em layout] para el establecimiento.
          li.d-flex.mb-4
            i.fas.fa-circle-check
            p.mb-0 #[b Fase de diseo de la disposicin de los elementos que deben decorar el almacn.] Se procede a crear el #[em layout] en el almacn. La exhibicin debe estar lista y los muebles deben tener todo el contenido de artculos, sealtica y PLV.
      .col-lg-5.col-md-9.col-9
        img(src='@/assets/curso/tema2/31.png')
    
    h4.mb-4(data-aos="fade-right") Caractersticas del #[em layout]
    p.mb-0(data-aos="fade-left") Disminuye la cantidad de movimientos del surtido, adems de su manipulacin, mermas e incluso robos, lo que hace que el inventario sea exacto. Tambin hace que el establecimiento tenga una buena distribucin evitando zonas congestionadas y, por ltimo, facilita la gestin de mantenimiento del surtido (Martnez, 2016).
    

    separador
    #t_2_4.titulo-segundo.mb-5
      h2 2.4	La exhibicin de productos

    p.mb-4(data-aos="fade-right") Consiste en la seleccin, ubicacin y exposicin de los artculos en el punto de venta, de tal forma que llamen la atencin de los clientes motivndolos a una accin de compra. Segn sea el tamao y tipo de establecimiento va a condicionar la distribucin y exhibicin de los productos.
    p.mb-4(data-aos="fade-left") La exposicin de los artculos en un almacn o establecimiento se hace mediante el uso de un mobiliario especial que permite mostrar al cliente la mayor cantidad de productos, durante el mayor tiempo posible.
    p.mb-4(data-aos="fade-right") Los objetivos de la exhibicin son:

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .col-lg-4.col-md-9.col-9
        img.mb-4(src='@/assets/curso/tema2/32.png')
      .col-lg-6
        ul.lista-ul
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Llamar la atencin del cliente para que vea los productos.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Generar fidelidad hacia el establecimiento.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Exhibir los productos.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Ofrecer comodidad al cliente.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Disponer de pasillos amplios entre los muebles exhibidores.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Apoyar las promociones de venta (descuentos, ofertas, 2x1, etc.).
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Recordar al cliente la presencia de un producto que pueda estar necesitando.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Motivar la accin de compra por impulso.

    p.mb-4(data-aos="fade-right") Segn sea la disposicin del establecimiento, el administrador que es el responsable del lugar y de la exhibicin, tiene como objetivo organizarlo segn:

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .col-lg-6.mb-3
        ol.lista-ol--cuadro
          li.d-flex
            .lista-ol--cuadro__vineta
              span 1
            p.mb-0 Situacin de las secciones.
          li.d-flex
            .lista-ol--cuadro__vineta
              span 2
            p.mb-0 La circulacin.
          li.d-flex
            .lista-ol--cuadro__vineta
              span 3
            p.mb-0 Lugares y puntos de venta fros y calientes.
          li.d-flex
            .lista-ol--cuadro__vineta
              span 4
            p.mb-0 Elementos en el exterior del punto de venta.
          li.d-flex
            .lista-ol--cuadro__vineta
              span 5
            p.mb-0 reas complementarias: bodegaje, servicios adicionales y entretenimiento.
      .col-lg-4.col-md-9.col-9
        img(src='@/assets/curso/tema2/33.png')
      
    p.mb-4(data-aos="fade-right") Una vez organizado el interior del establecimiento, determinadas las diferentes secciones y definidos los espacios de los pasillos, se puede considerar el uso de los tipos de exhibicin y el #[em layout] del surtido.

    .bg6.mb-4(data-aos="fade-right")
      .row.justify-content-center.align-items-center
        .col-lg-5.col-7.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/tema2/34.png', alt='')
        .col-lg-7(data-aos="fade-left")
          .p-4
            h4.mb-3 Tipos de exhibicin
            p.mb-3 No importa cul sea el tipo de exhibicin que se elija en el establecimiento, deber motivar en el cliente la accin de compra, por lo que se deben tener en cuenta las actividades programadas en el punto de venta, lo mismo que la temporada y los objetivos de venta. De esta manera, se puede pensar en tener exhibiciones especiales y exhibiciones secundarias o adicionales (por fuera del lineal y la gndola).
            p.mb-3 Una exhibicin debe contar con una excelente exposicin de los productos para poder llamar la atencin del pblico que con frecuencia visita el establecimiento. En cada seccin se debe emplear un buen #[em merchandising] y utilizar los mejores elementos para su produccin, lo cual es vital en la rotacin del artculo que forme parte de la exhibicin.
            p.mb-0 Por lo general, los clientes toman las decisiones de compra directamente en el punto de venta, por lo que una efectiva exhibicin de los artculos puede influir positivamente en la toma de decisin.
    
    p.mb-4(data-aos="fade-right") Los siguientes son algunos de los tipos de exhibicin ms comunes:
    
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8
        ol.lista-ol--cuadro
          li.d-flex 
            .lista-ol--cuadro__vineta
              span 1
            p.mb-0 #[b Exhibicin vertical.] Los productos se ubican de manera que cubran todos los niveles del lineal o gndola. Los de tamaos ms pequeos van arriba, los medianos van en medio y los de mayor tamao en los niveles bajos del anaquel.
          li.d-flex 
            .lista-ol--cuadro__vineta
              span 2
            p.mb-0 #[b Exhibicin horizontal.] El producto se ubica en sus diferentes variedades, a lo largo de la estantera.
          li.d-flex 
            .lista-ol--cuadro__vineta
              span 3
            p.mb-0 #[b Exhibicin por bloques.] El producto se ubica en bloques en la estantera.
          li.d-flex 
            .lista-ol--cuadro__vineta
              span 4
            p.mb-0 #[b Exhibicin de doble franja.] En la misma estantera se exhiben dos lneas de productos relacionados: una lnea de productos de gran consumo o bsicos y otra lnea de productos de impulso, como dulces, chocolates, chicles y paletas.
          li.d-flex 
            .lista-ol--cuadro__vineta
              span 5
            p.mb-0 #[b Exhibicin en #[em check out.]] Productos que se encuentran cerca de las cajas para que el cliente realice una accin de compra por impulso en el momento de hacer la fila para pagar.
          li.d-flex 
            .lista-ol--cuadro__vineta
              span 6
            p.mb-0 #[b Exhibicin especial.] Tiene un diseo distinto al de un lineal, gndola o estantera. Puede ser un exhibidor exclusivo para el producto o una isla, y se utiliza generalmente durante promociones de venta o lanzamientos de nuevos productos.
      .col-lg-4.col-md-7.col-8
        img(src='@/assets/curso/tema2/35.svg')

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10
        .cajon.color-secundario.p-4
          p.mb-0 La cantidad de formas para exhibir los productos en el punto de venta es muy amplia y variada. Lo importante es que son aplicables a cualquier establecimiento comercial, de tal forma que los espacios sean aprovechados al mximo al interior del local. Por esta razn, es vital planear adecuadamente en qu forma se van a exhibir los artculos, cmo se van a distribuir los lineales, las gndolas, los muebles, las vitrinas, el escaparate y dems elementos que le darn forma al establecimiento.
    
    p.mb-5(data-aos="fade-right") Exhibiciones adicionales o complementarias:

    .row.fondo1.align-items-center.p-3.mb-5(data-aos="fade-down")
      .col-lg-12.col-md-12.col-12.px-lg-5.px-4
        .row.justify-content-center.mb-4
        
        .tarjetaf.p-4.mb-4
          SlyderA(tipo="b").slider-a-custom(data-aos="zoom-in-left")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Exhibicin emblemtica o especial
                p.mb-4 Es un #[em display] que busca destacar las caractersticas especficas de los productos. Puede ser diseado de muchas formas.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/36.svg")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Exhibicin en islas
                p.mb-4 Es una forma de presentacin masiva y solitaria de un artculo. Se ubican fuera del lineal o gndola y no tienen contacto con otro tipo de artculos.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/37.svg")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Exhibicin de ganchos
                p.mb-4 Estn ubicados en las esquinas de las gndolas, por lo que tienen un mayor impacto, aumentando la comunicacin visual, con accesorios que lo hacen ms atractivo como cabezotes, laterales, frontales, cenefas y partes mviles. Ayudan a posicionar una marca.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/38.svg")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Exhibicin en punta de gndola
                p.mb-4 Estn ubicados en las esquinas de las gndolas, por lo que tienen un mayor impacto, aumentando la comunicacin visual, con accesorios que lo hacen ms atractivo como cabezotes, laterales, frontales, cenefas y partes mviles. Ayudan a posicionar una marca.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/39.svg")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Exhibicin mamut
                p.mb-4 Son de gran tamao, verstiles y con un excelente contenido escenogrfico, cuyo fin es presentar variedad de productos de una marca en el mismo lugar personalizndolo para impactar visualmente al cliente a travs de diseos ldicos.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/40.svg")

    p.mb-4(data-aos="fade-right") Una exhibicin debe tener un grupo de caractersticas que avalen su funcionalidad y logren que se cumplan los objetivos del establecimiento. Por esta razn, la exhibicin debe ser visible y atractiva a los ojos del cliente, adems de comunicar efectivamente, ya que el #[em merchandising] es comunicacin visual en el interior del establecimiento.
    
    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2 Caractersticas de la exhibicin  
        
    p.mb-4(data-aos="fade-left") En primer lugar, se recomienda que la exhibicin ambiente el punto de venta, lo cual debe venir con una presentacin llamativa de los artculos, adems de colorida, imponente y al alcance de los clientes, con precios justos y cuando sea posible, con degustaciones que hagan que el consumidor conozca de primera mano un producto. 
    p.mb-0(data-aos="fade-left") Una exhibicin debe reunir las siguientes caractersticas: sencilla, visible, atractiva, accesible, comunicativa, rellenable, segura, alegre, limpia, estable y con precio visible (Cruz, 2018).

    separador
    #t_2_5.titulo-segundo.mb-5
      h2 2.5	Vitrinismo o visual #[em merchandising]

    .row.justify-content-center.align-items-center(data-aos="fade-left").mb-4
