          figcaption Video. Visual #[em Merchandising]
    
    .row.justify-content-center.align-items-center(data-aos="fade-left").mb-4
      .col-lg-8.mb-3
        p.mb-4 Son todas las tcnicas y estrategias que se emplean en el diseo de un escaparate o vitrina con el fin de conquistar los clientes que transitan en el establecimiento, incitarlos e invitarlos a que realicen la compra de los productos exhibidos. El vitrinismo funciona como comunicador visual y conceptual que funciona en pro de conquistar al consumidor. La vitrina es un lienzo en blanco donde puede formar toda la creatividad para convertirla en una obra de arte (Borja, 2021).
        p.mb-4 Segn Joan Costa, la vitrina tiene un tiempo de percepcin de 8 segundos para atraer la atencin de un cliente. Por ello, debe disearse con argumentos comunicacionales que resalten los artculos, combinando sencillez y creatividad. La idea, la composicin, el color, las piezas grficas, la lnea y la iluminacin deben trabajar en conjunto para crear una vitrina llamativa, impactante y una experiencia emocional e inolvidable para el cliente (Costa, 2007).
