    #t_2_6.titulo-segundo.mb-5
      h2 2.6	#[em Merchandising]

    .row.justify-content-center.align-items-center(data-aos="fade-left").mb-5
      .col-lg-12.col-12.col-md-12
        figure(data-aos="fade-right").py-2
          .video.mb-1
            iframe(width="560" height="315" src="https://www.youtube.com/embed/2HWT5IqEZds?si=bgEq0O1EVxpqkn34" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen)
          figcaption Video. #[em Merchandising] en el punto de venta

    .row.justify-content-center.align-items-center(data-aos="fade-left").mb-4
      .col-lg-7
        p.mb-4 Es el conjunto de estrategias y tcticas efectuadas dentro del establecimiento y que tiene como fin buscar el artculo correcto, en la cantidad necesaria, con el precio justo, en el tiempo adecuado, con una presentacin visual impactante y dentro de una exhibicin sobresaliente. Se refiere a todos los esfuerzos destinados a que el cliente, no solo conozca el producto, sino tambin, que tome una accin de compra.
        p.mb-4 El trmino #[em merchandising], como se conoce en la actualidad, se increment con la aparicin del autoservicio en Estados Unidos en la dcada de 1930. poca en la que los establecimientos se dieron cuenta del impacto e influencia que tenan las exhibiciones en el consumidor. Los artculos que estaban exhibidos tenan una mejor respuesta comercial dando comienzo a una serie de investigaciones en el tema, que terminaron por profesionalizar el rea. Y fue as como, con el paso de los aos, los supermercados comenzaron a usar el #[em merchadising] para destacar y promocionar toda la mercanca en sus lugares de venta (Borja, 2021).
      .col-lg-5.col-9.col-md-9
        img(alt="" src="@/assets/curso/tema2/50.png")
    
    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2 Tipos de #[em merchandising]
    
    p.mb-5(data-aos="fade-right") Existen diferentes opciones para destacar los productos en el punto de venta, que dan mayores posibilidades para mejorar la exhibicin de las marcas y sus artculos. Y para lograrlos, hay varios tipos de merchandising: segn la naturaleza, segn el tipo de cliente y segn el ciclo de vida del producto, las cuales se presentarn a continuacin:
  
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .sub.mb-4(data-aos="fade-right")
          .d-flex.align-items-center.mb-1
            img(src='@/assets/curso/tema2/ico.svg', alt='')
            h3.mb-0.ms-2 Segn la naturaleza
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12   
        .tarjeta--container.row.mb-4
          .col-md.tarjeta.color-primario.bd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center #[em Merchandising] de presentacin
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/51.svg', alt='Texto que describa la imagen')
            p Se presentan los productos de modo que sea fcil de verlos por parte del cliente y adems rentable para el establecimiento.
          .col-md.tarjeta.color-primario.bdd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center #[em Merchandising] de gestin
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/52.svg', alt='Texto que describa la imagen')
            p Gestiona el espacio o lugar de venta para obtener el mayor rendimiento en cada lineal o gndola.
          .col-md.tarjeta.color-primario.bd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center #[em Merchandising] de seduccin
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/53.svg', alt='Texto que describa la imagen')
            p Da un aspecto seductor, no solo al lineal, sino a la tienda en general. Tambin se conoce como tienda espectculo por el diseo especial que se crea para el impacto visual al cliente.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .sub.mb-4(data-aos="fade-right")
          .d-flex.align-items-center.mb-1
            img(src='@/assets/curso/tema2/ico.svg', alt='')
            h3.mb-0.ms-2 Segn el tipo de cliente
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8 
        .tarjeta--container.row.mb-4
          .col-md.tarjeta.color-primario.bd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Para el cliente  #[em shopper] 
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/54.svg', alt='Texto que describa la imagen')
            p El cliente que requiere de un grupo de argumentos para visitar un establecimiento en lugar de otro.
          .col-md.tarjeta.color-primario.bdd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Para el cliente #[em buyer]
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/55.svg', alt='Texto que describa la imagen')
            p Se refiere al cliente que ya se encuentra dentro del establecimiento y que se basa en el precio, la calidad del producto, las promociones de venta, la gama de productos y la atencin recibida para tomar una decisin de compra.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .sub.mb-4(data-aos="fade-right")
          .d-flex.align-items-center.mb-1
            img(src='@/assets/curso/tema2/ico.svg', alt='')
            h3.mb-0.ms-2 Segn el ciclo de vida del producto
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        .tarjeta--container.row.mb-4
          .col-md.tarjeta.color-primario.bd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Producto de lanzamiento
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/56.svg', alt='Texto que describa la imagen')
            p Cuando una nueva referencia, bien o servicio sale al mercado, es necesario definir su emplazamiento, ubicacin y lineal donde ser exhibido. 
          .col-md.tarjeta.color-primario.bdd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Producto de ataque
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/57.svg', alt='Texto que describa la imagen')
            p Cuando el producto crece rpidamente, se posiciona y es el mejor frente a los dems productos de la familia. En ese momento se puede darle un espacio ms amplio en el lineal.
          .col-md.tarjeta.color-primario.bd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Producto de mantenimiento
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/58.svg', alt='Texto que describa la imagen')
            p Cuando el producto ya lleg a su madurez, est posicionado y tiene un buen lugar y espacio en el lineal. En este momento se debe tratar de que esta situacin permanezca la mayor cantidad de tiempo posible.
          .col-md.tarjeta.color-primario.bdd.p-4
            .row.justify-content-center.mb-4
              h4.mb-4.text-center Producto de defensa
              .col-8
                figure
                  img.img-a.img-t(src='@/assets/curso/tema2/59.svg', alt='Texto que describa la imagen')
            p Cuando el producto se encuentra en su fase de declive y es cuando el espacio puede disminuir o incluso desaparecer del lineal. El fabricante debe decidir si lo retira del mercado o si le inyecta un cambio que sea positivo para el cliente.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .sub.mb-4(data-aos="fade-right")
          .d-flex.align-items-center.mb-1
            img(src='@/assets/curso/tema2/ico.svg', alt='')
            h3.mb-0.ms-2 Ciclo de vida del producto
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8
        .titulo-sexto.color-secundario.mb-4
          h5 Figura 16. 
          | Ciclo de vida del producto
        img(src="@/assets/curso/tema2/60.svg" alt='Presenta el ciclo de vida de un producto en cuanto a las ventas en el tiempo, inicialmente se realiza una introduccin del mismo, se impulsa el crecimiento para lograr la madurez en el ventas, posteriormente presenta un declive.')

    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2 Funciones del #[em merchandising]

    p.mb-4(data-aos="fade-right") La labor de crear #[em merchandising] es ubicar, apoyar, monitorear y actualizar los productos, adems de tener la exhibicin impecable y apoyar los productos con publicidad que los acompae (Borja, 2021). 
    
    .row.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-8
        p.mb-4 Las principales funciones del #[em merchandising] son:
        ul.lista-ul
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Exposicin lo ms ptima posible de los artculos.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Garanta de que los artculos se encuentran disponibles para el cliente.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Rectitud en el suministro de productos de la empresa comercial.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Planificacin del espacio de exhibicin.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Impacto visual del producto destacndolo de los dems grupos de productos similares de la competencia.
          li.d-flex.mb-0
            i.fas.fa-circle-check
            p.mb-0 Uso estratgico del espacio del lineal, gndola o estantera. 
      .col-lg-4.col-9.col-md-9(data-aos="fade-left")
        img(alt="" src="@/assets/curso/tema2/61.png")
    
    separador
    #t_2_7.titulo-segundo.mb-5
      h2 2.7	Sealizacin comercial

    p.mb-4(data-aos="fade-right") Es una herramienta muy importante que ayuda a mejorar la informacin dentro del establecimiento, de tal forma que el visitante encuentre fcil el producto que necesita. Por lo tanto, ayuda a la circulacin y la orientacin dentro del recinto. Una buena implementacin de un proyecto de sealizacin debe tener en cuenta los siguientes dos aspectos (Costa, 2007).
  
    TabsC.color-primario(data-aos="zoom-in-left").mb-5
      .py-md-4(titulo="Localizacin previa del sitio")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/62.png', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 Localizacin previa del sitio
            p.mb-0 Diagnsticos del marco legal determinado y derroteros en el reconocimiento del punto de venta. Ayuda para comprender cules son los condicionantes que se deben tener en cuenta a la hora de disear la sealtica. Por otro lado, se debe tener en cuenta qu tipo de signos se van a usar, para que el creativo entre en accin y disee las diferentes piezas, donde debe tener en cuenta el espacio disponible, tamao de las piezas, colores, texturas, materiales. El diseo que desarrolle el creativo debe ser coherente a la identidad y personalidad de la marca, y debe ir de acuerdo con las normativas del sitio. Una vez el diseo est listo y aprobado se pasa a la etapa de produccin e instalacin.
      .py-md-4(titulo="Sealizacin del establecimiento comercial")
        .row.justify-content-center.align-items-center
          .col-lg-3.col-md-7.col-9.my-3
            img.img-a.img-t(src='@/assets/curso/tema2/63.png', alt='')
          .col-lg-8.col-md-12.my-3
            h3.mb-4 Sealizacin del establecimiento comercial
            p.mb-4 El diseo de la sealizacin debe tener armona con la arquitectura del establecimiento. El trabajo interdisciplinario, en este momento es fundamental, porque se debe tener en cuenta la opinin de arquitectos, diseadores industriales y tcnicos especialistas en el tema. Con esto se asegura un ptimo resultado en el cumplimiento de los objetivos de sealizacin en el punto de venta.
            p.mb-0 La finalidad de la sealizacin comercial es la de hacer un llamado de atencin al pblico presente sobre algunas situaciones de riesgo de una manera simple, rpida y de fcil comprensin. Cuando la sealizacin de seguridad falta en el interior de un establecimiento incrementa las situaciones de riesgo, porque priva a empleados y visitantes de una elemental informacin bsica que puede alertar sobre el riesgo y la forma en que se puede evitar (Costa, 2007).
  
    .subt.mb-4(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
        img(src='@/assets/curso/tema1/ic.svg', alt='')
        h3.mb-0.ms-2 Tipos de sealizacin comercial
    
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .col-lg-8.col-md-12.col-12.mb-3
        p.mb-4 Para que las seales sean lo ms efectivas posible, deben cumplir ciertas consideraciones que tienen que ver con la ubicacin en el almacn, la forma, el color y el diseo. Adems, se debe tener en cuenta:
        .cajon.color-acento-contenido.p-4.mb-4
          p.mb-4 #[b El material:] que resista los choques.
          p.mb-4 #[b El tamao:] que sea el adecuado de tal forma que garantice su visibilidad y comprensin.
          p.mb-0 #[b La ubicacin:] que est en una adecuada altura y en una apropiada posicin. Se debe tener en cuenta los posibles obstculos que interfieran su visibilidad y el ngulo con respecto a la visual del cliente.
        p.mb-0 En Colombia existen leyes y resoluciones relacionadas con las normas en sealizacin, que buscan asegurar la integridad y salud del ser humano en el mbito laboral y en lugares pblicos, como se cita a continuacin. 
      .col-lg-4.col-md-9.col-9
        img.d-none.d-lg-block(src='@/assets/curso/tema2/64.png', alt='tema1')
    
    .row.justify-content-center.mb-4
      .col-lg-4(data-aos="fade-left" data-aos-delay="500")
        figure.m-auto.col-7.col-md-7.col-lg-12
          img.mb-4(src="@/assets/curso/tema2/65.png") 
      .col-lg-8.col-md-12.col-12
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.col-xl-11.m-auto(titulo="Ley 9 de 1979: Ttulo III. (Salud Ocupacional)")
            p.mb-0 Donde se estipulan normas para preservar, conservar y mejorar la salud de los individuos en sus ocupaciones.
          .row.col-xl-11.m-auto(titulo="Resolucin 2400 de mayo 22 de 1979")
            p.mb-0 Por la cual se establecen algunas disposiciones sobre vivienda, higiene y seguridad en los establecimientos de trabajo.
          .row.col-xl-11.m-auto(titulo="Ttulo V. Artculo 202. Cdigo de Colores")
            p.mb-0 En todos los establecimientos de trabajo en donde se lleven a cabo operaciones y/o procesos que integren (aparatos, mquinas, equipos, ductos, tuberas, etc.); Y dems instalaciones locativas necesarias para su funcionamiento que utilizarn los colores bsicos recomendados por la AMERICAN STANDARS ASSOCIATION (ASA) y otros colores especficos, para identificar los elementos, materiales, etc., y dems elementos especficos que determinen o prevengan riesgos que puedan causar accidentes o enfermedades profesionales. 
          .row.col-xl-11.m-auto(titulo="Decreto 1443 de 2014")
            p.mb-0 Por medio del cual se dictan disposiciones para la implementacin del Sistema de Gestin de la Seguridad y Salud en el Trabajo (SG-SST).
          .row.col-xl-11.m-auto(titulo="Decreto 1072 de 2015, especialmente la parte referida al SG-SST")
            p.mb-0 Est especialmente enfocada a OSHAS 18001, indicando cmo definir unos parmetros para lograr la mejora continua.
  
    p.mb-5(data-aos="fade-right") Adems, existe una normativa que establece los parmetros que se deben cumplir en el diseo de las seales de seguridad. Se trata de la norma tcnica Colombiana NTC 1461, que tiene como fin instaurar los colores y seales de seguridad que deben ser usados para prevenir accidentes y riesgos contra la salud e integridad de las personas, adems de situaciones de emergencia. Esta norma se apoya en las normas internacionales ISO 3864-1 e ISO 16069:2004, que establecen varios criterios para el diseo de estas. A continuacin, se exponen algunos de esos criterios:
  
