          p.mb-0 #[b Sealtica corporativa.] La sealtica corporativa es caracterstica de cada empresa y acta como gua para las personas en un espacio pblico. No solo cumple la funcin de orientar, sino que tambin refuerza la identidad de la marca y ayuda a generar recordacin en los clientes. El diseo de la sealtica puede variar segn el establecimiento, su tamao y sus condiciones, y puede incluir seales colgadas o pegadas en la pared. Lo crucial es que la sealtica cumpla una funcin comunicativa visual completa y eficaz. Sirve para dirigir a las personas hacia diferentes reas, como dependencias, baos pblicos, rutas de evacuacin, botes de basura, salidas de emergencia, entre otros (Costa, 2007). 
    
    separador
    #t_2_8.titulo-segundo.mb-5
      h2 2.8	Dispositivos de seguridad
    
    p.mb-4(data-aos="fade-right") Son unos sistemas que usan los establecimientos, principalmente a la hora de exhibir los artculos, y sirven para avisar al personal en el preciso momento en el que pueda estar existiendo un robo con el fin de evitar el hurto del artculo, interponiendo medios fsicos que tienen como funcin dificultar la accin la sustraccin por parte del ladrn (Vidal, Mas y Gonzlez, 2011).
    p.mb-5(data-aos="fade-left") Un establecimiento puede dejar de tener rentabilidad a causa del hurto de artculos dentro del punto de venta. Es por esta razn que para la prevencin de estos acontecimientos indeseados se aconseja lo siguiente:
    
    .row.mb-5
      .col-lg-12.mb-4.mb-lg-0(data-aos="fade-right")
        TabsA.color-primario
          .tarjeta.color-acento-botones.bg7.p-4(titulo="Organizar")
            .row.justify-content-center.align-items-center
              .col-lg-12
                p.mb-4 #[b Organizar el local para evitar los robos]
                p.mb-4 Mantener el establecimiento ordenado es crucial para prevenir robos en el punto de venta, ya que permite una visin clara de todo el espacio. Esto facilita la deteccin rpida de cualquier faltante, ya que el espacio vaco indicar la ausencia de un artculo. Es esencial conocer a fondo el punto de venta.
                p.mb-0 Es importante considerar la ubicacin de las cajas registradoras:  estn situadas en lugares que permitan una visin clara de las entradas y salidas del establecimiento? Un anlisis detallado del espacio ayuda a identificar y prevenir posibles riesgos de robo. Adems, para eliminar los puntos ciegos, se pueden instalar espejos estratgicamente y elegir el mobiliario adecuado que no interfiera con la visibilidad del rea de venta.
          .tarjeta.color-acento-botones.bg7.p-4(titulo="Aportar")
            .row.justify-content-center.align-items-center
              .col-lg-12
                p.mb-4 #[b Aportar formacin a los empleados]
                p.mb-4 Adems de tener el punto de venta vigilado, se recomienda que los empleados se capaciten para prevenir los robos y saber cmo actuar en caso de un evento de esta magnitud.
                p.mb-0 Conociendo las tcnicas ms productivas de vigilancia, los protocolos de actuacin en caso de hurto y los ms sospechosos comportamientos por parte de los clientes, ayudan de una forma ms efectiva a mantener el establecimiento seguro.
          .tarjeta.color-acento-botones.bg7.p-4(titulo="Prestar atencin")
            .row.justify-content-center.align-items-center
              .col-lg-12
                p.mb-4 #[b Prestar atencin a los puntos ms indefensos y artculos atractivos]
                p.mb-4 Se refiere a aquellos espacios como son los probadores o las salidas de emergencia. Son los lugares ms comunes usados por los ladrones para poderse escapar con los productos robados.
                p.mb-0 Se recomienda transitar por el local, lo cual va a permitir ver con claridad a los visitantes que entran y salen del negocio. De esta forma se pueden ver y analizar las acciones de los ladrones y reaccionar pronto en caso de que exista un robo.
          .tarjeta.color-acento-botones.bg7.p-4(titulo="Contar")
            .row.justify-content-center.align-items-center
              .col-lg-12
                p.mb-4 #[b Contar con un detector de metales]
                p.mb-4 El uso de bolsas envueltas con aluminio evita los sistemas de seguridad, por lo que es una tcnica muy usada por parte de los ladrones.
                p.mb-0 Pero, para ello, existen unos dispositivos que permiten detectar el ingreso de estos productos al establecimiento, siendo reconocidos fcilmente como ladrones, desde antes de ingresar al punto de venta.
          .tarjeta.color-acento-botones.bg7.p-4(titulo="Saber identificar")
            .row.justify-content-center.align-items-center
              .col-lg-12
                p.mb-4 #[b Saber identificar los hbitos de robo ms comunes]
                p.mb-4 Los ladrones trabajan en equipo, porque as suelen distraer la atencin del personal. Comportamientos extraos como, por ejemplo, observar detalladamente un producto o permanecer mucho tiempo al frente de un producto en especial, son actitudes que se deben identificar.
                p.mb-0 Entre otros hbitos se destacan el uso de ropa amplia, como chaquetas o pantalones con varios bolsillos o tulas grandes.
          .tarjeta.color-acento-botones.bg7.p-4(titulo="Implantar")
            .row.justify-content-center.align-items-center
              .col-lg-12
                p.mb-4 #[b Implantar sistemas de seguridad]
                p.mb-4 Es vital proteger el punto de venta a travs de diferentes sistemas que incrementen la seguridad del establecimiento.
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
      .col-lg-10.col-md-12.col-12
