        h3.mb-0.ms-2 Sistemas antihurto para tiendas de ropa
    
    p.mb-4(data-aos="fade-right") Para no tener gastos a causa de los robos se recomienda invertir en un sistema de seguridad instalado en el punto de venta. Son muy tiles porque registran a los ladrones en cuanto tengan la mnima oportunidad de realizar un robo. Con estos sistemas se pueden exhibir los productos en lugares asequibles al cliente, sin necesidad de ubicarlos detrs de un exhibidor o en una vitrina cerrada y bajo llave. As, los clientes podrn ver los artculos sin problema alguno y, por ende, se puede evitar la molestia al empleado de estar sacando constantemente los artculos de la vitrina.
    p.mb-5(data-aos="fade-left") Para proteger los productos, los establecimientos pueden disponer de diferentes sistemas o equipos de seguridad, como, por ejemplo:

    .row.fondo1.align-items-center.p-3.mb-5(data-aos="fade-down")
      .col-lg-12.col-md-12.col-12.px-lg-5.px-4
        .row.justify-content-center.mb-4
        
        .tarjetaf.p-4.mb-4
          SlyderA(tipo="b").slider-a-custom(data-aos="zoom-in-left")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Alarmas
                p.mb-4 Sistema que no evita el hurto, pero s advierte de su presencia a quien entra al punto de venta. Se conecta a una central receptora, en la que hay alguien pendiente de lo que sucede en el establecimiento. Su ms importante funcin es disuadir cualquier intento de hurto. Si se quiere, pueden tener un diseo discreto y se pueden camuflar de forma esttica en el entorno en el que se instalen.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/76.png")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Vigilantes de seguridad
                p.mb-4 Personal dedicado a la vigilancia y seguridad en los establecimientos. Tienen funciones como, evitar cualquier acto delictivo en cualquier momento, denunciar ante infracciones administrativas y proteger los artculos y el negocio como tal.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/77.png")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Vitrinas cerradas con llave
                p.mb-4 Escaparates en los que se exhiben los productos ofertados por el establecimiento. Se distinguen por tener una distinguida presentacin y por exhibir productos de alto valor, dndoles una mejor proteccin. Ayudan a la conservacin de los artculos porque los aslan del polvo, la humedad, el aire y tambin de los ladrones.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/78.png")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 Etiquetas antihurto adhesivas
                p.mb-4 Son uno de los sistemas antihurto ms usados, porque cuenta con sensores de alarma que activan los arcos antihurto cuando se produce un intento de robo. En el momento de la compra del artculo se desactivan o se quitan. Algunos tipos de productos que pueden tener este sistema son joyas, gafas, ropa, perfumes, cosmticos, bebidas, pequeos electrodomsticos, cmaras de video y fotos, videojuegos y consolas, celulares, computadores, entre muchos otros.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/79.png")
            .row.justify-content-center.align-items-center.p-0.p-lg-4
              .col-lg-7.col-12
                h4.mb-4 CCTV (Circuitos Cerrados de Cmara de Televisin)
                p.mb-4 Sistema que usa tecnologa de videovigilancia que se emplea para supervisar diversas zonas del establecimiento. Cubren un rango amplio y con muy buen detalle visual. Presenta imgenes de alta definicin e identifica de forma efectiva a los ladrones. Por otro lado, tiene visin nocturna y detector de movimiento.
              .col-lg-4.col-md-7
                img(alt="" src="@/assets/curso/tema2/80.png")
    
    .row.justify-content-center.align-items-center(data-aos="fade-left")
      .col-lg-10.col-md-12.col-12
        .cajon.color-acento-contenido.p-4
          p.mb-0 Tener estos sistemas de seguridad le permitir al establecimiento tener mejores garantas de obtener la rentabilidad esperada, porque reducen notablemente los intentos de robo de mercanca (Vidal, Mas y Gonzlez, 2011).

